export default class Preview {
  constructor() {
    this.timerId = 0;
  }

  eventForStartAnimation() {
    if (document.querySelector('.animation-field').querySelector('img')) {
      clearInterval(this.timerId);
      document.querySelector('.animation-field').removeChild(document.querySelector('.animation-field').querySelector('img'));
    }

    const images = document.querySelector('.frames').querySelectorAll('img');
    if (images.length === 0) return;

    const img = document.createElement('img');
    // img.style.width = '190px';
    // img.style.height = '190px';
    img.setAttribute('width', '190');
    img.setAttribute('height', '190');
    document.querySelector('.animation-field').appendChild(img);

    const speed = parseInt(document.getElementById('speed').getAttribute('value'), 10);

    let i = 0;
    this.timerId = setInterval(() => {
      img.setAttribute('src', images[i].currentSrc);
      i += 1;
      if (images.length === i) i = 0;
    }, 1000 / speed);
  }

  eventForStopAnimation() {
    clearInterval(this.timerId);
    if (!document.querySelector('.animation-field').querySelector('img')) return;
    document.querySelector('.animation-field').removeChild(document.querySelector('.animation-field').querySelector('img'));
  }

  static eventForChangeSpeed() {
    const range = document.getElementById('speed');
    range.setAttribute('value', parseInt(range.value, 10));
    range.nextElementSibling.innerHTML = `${parseInt(range.value, 10)} FPS`;
  }

  static eventForFullScreen() {
    document.querySelector('.animation-field').requestFullscreen();
  }

  start() {
    document.getElementById('start-animation').addEventListener('click', this.eventForStartAnimation);
    document.getElementById('stop-animation').addEventListener('click', this.eventForStopAnimation);
    document.getElementById('speed').addEventListener('input', Preview.eventForChangeSpeed);
    document.getElementById('fullscreen').addEventListener('click', Preview.eventForFullScreen);
  }
}

