import html2canvas from 'html2canvas';

export default class Frames {
  static eventAddFrame() {
    const frame = document.createElement('div');
    frame.classList.add('frames__frame');

    const copyForFrame = document.createElement('div');
    copyForFrame.classList.add('frame__copy');

    const deleteForFrame = document.createElement('div');
    deleteForFrame.classList.add('frame__delete');

    frame.appendChild(deleteForFrame);
    frame.appendChild(copyForFrame);

    html2canvas(document.getElementById('canvas')).then((canvas) => {
      const imageCanvas = canvas.toDataURL();
      const img = document.createElement('img');
      img.setAttribute('src', imageCanvas);
      img.style.width = '110px';
      img.style.height = '110px';
      frame.appendChild(img);
    });

    const frames = document.querySelector('.frames');
    frames.insertBefore(frame, document.querySelector('.frames__add-frame'));
  }

  static eventForDelete(e) {
    const { target } = e;
    if (target.className === 'frame__delete') {
      target.parentNode.parentNode.removeChild(target.parentNode);
    }
  }

  static eventForCopy(e) {
    const { target } = e;
    if (target.className === 'frame__copy') {
      const copyFrame = target.parentNode.cloneNode(target.parentNode);
      document.querySelector('.frames').insertBefore(copyFrame, target.parentNode.nextElementSibling);
    }
  }

  static start() {
    document.querySelector('.frames__add-frame').addEventListener('click', Frames.eventAddFrame);
    document.body.addEventListener('click', Frames.eventForDelete);
    document.body.addEventListener('click', Frames.eventForCopy);
  }

  static getFrames() {
    return document.querySelector('.frames').querySelectorAll('canvas');
  }
}
