import Frames from './src/components/Frames/Frames';
import Preview from './src/components/Preview/Preview';

let currentState = '';
let currentColor;
let prevColor;

function getCurrentColor() {
  const styles = getComputedStyle(document.getElementById('color-current'));
  return styles.backgroundColor;
}

function canvasStorageSaveColor() {
  const arrayItemColor = [];
  const elementsCanvas = document.querySelectorAll('.canvas__item');
  for (let i = 0; i < elementsCanvas.length; i += 1) {
    arrayItemColor.push(getComputedStyle(elementsCanvas[i]).backgroundColor);
  }
  localStorage.setItem('canvasColor', JSON.stringify(arrayItemColor));
}

function canvasStorageSaveShape() {
  const arrayItemShape = [];
  const elementsCanvas = document.querySelectorAll('.canvas__item');
  for (let i = 0; i < elementsCanvas.length; i += 1) {
    arrayItemShape.push(elementsCanvas[i].classList.contains('circle'));
  }
  localStorage.setItem('canvasShape', JSON.stringify(arrayItemShape));
}

document.getElementById('transform').addEventListener('click', () => {
  currentState = 'transform';
  document.body.style.cursor = 'url(assets/images/i4.png), auto';
});

document.addEventListener('keydown', (e) => {
  if (e.shiftKey && e.keyCode === 82) {
    currentState = 'transform';
    document.body.style.cursor = 'url(assets/images/i4.png), auto';
  }
});

document.getElementById('paint-bucket').addEventListener('click', () => {
  currentState = 'paint-bucket';
  document.body.style.cursor = 'url(assets/images/i1.png), auto';
});

document.addEventListener('keydown', (e) => {
  if (e.shiftKey && e.keyCode === 81) {
    currentState = 'paint-bucket';
    document.body.style.cursor = 'url(assets/images/i1.png), auto';
  }
});

document.getElementById('choose-color').addEventListener('click', (e) => {
  currentState = 'choose-color';
  document.body.style.cursor = 'url(assets/images/i2.png), auto';
  e.stopPropagation();
});

document.addEventListener('keydown', (e) => {
  if (e.shiftKey && e.keyCode === 87) {
    currentState = 'choose-color';
    document.body.style.cursor = 'url(assets/images/i2.png), auto';
    e.stopPropagation();
  }
});

document.getElementById('cancel').addEventListener('click', () => {
  currentState = '';
  document.body.style.cursor = 'default';
});

document.addEventListener('click', (e) => {
  const targ = e.target;
  if (targ.classList.contains('canvas__item')) {
    if (currentState === 'transform') {
      targ.classList.toggle('circle');
      canvasStorageSaveShape();
    }

    if (currentState === 'paint-bucket') {
      targ.style.backgroundColor = getCurrentColor();
      canvasStorageSaveColor();
    }
  }
});

document.body.addEventListener('click', (e) => {
  const targ = e.target;
  if (currentState === 'choose-color') {
    prevColor = getCurrentColor();
    document.getElementById('color-prev').style.backgroundColor = prevColor;
    localStorage.setItem('prevColor', JSON.stringify(prevColor));

    currentColor = window.getComputedStyle(targ).backgroundColor;
    document.getElementById('color-current').style.backgroundColor = currentColor;
    localStorage.setItem('currentColor', JSON.stringify(currentColor));
  }
});

document.addEventListener('DOMContentLoaded', () => {
  prevColor = JSON.parse(localStorage.getItem('prevColor'));
  document.getElementById('color-prev').style.backgroundColor = prevColor;

  currentColor = JSON.parse(localStorage.getItem('currentColor'));
  document.getElementById('color-current').style.backgroundColor = currentColor;

  const elementsCanvas = document.querySelectorAll('#canvas .canvas__item');
  const arrayItemColor = JSON.parse(localStorage.getItem('canvasColor'));
  const arrayItemShape = JSON.parse(localStorage.getItem('canvasShape'));
  for (let i = 0; i < elementsCanvas.length; i += 1) {
    elementsCanvas[i].style.backgroundColor = arrayItemColor[i];
    if (arrayItemShape[i] === true) {
      elementsCanvas[i].classList.add('circle');
    }
  }
});

Frames.start();

const preview = new Preview();
preview.start();
